import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { Store } from '@ngrx/store';
import { ActivatedRoute, Router } from '@angular/router';
import * as user from '../../reducers/user';
import { Observable } from 'rxjs/Observable';

import { UserService } from '../../services/user';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  user: Observable<user.State>;
  canLogIn = false;
  logging = false;
  error = false;
  errorMsg = '';
  _userName = '';
  _password = '';

  constructor(private store: Store<user.State>,
    private userService: UserService, private router: Router) {
      this.user = store.select('user');
      this.user.subscribe((s) => {
        this._userName = s.userName;
        this._password = s.password;
      });
    }

  ngOnInit() {
  }

  login(e: any) {
    this.logging = true;
    this.error = false;
    this.userService.login(this._userName, this._password)
    .then((res) => {
      if (res.success) {
        this.logging = false;
        setTimeout(() => {
          this.router.navigate(['actions']);
        }, 700);
      } else {
        this.showError(`Something happened trying to log ${this._userName} in`);
      }
    })
    .catch((res) => {
      setTimeout(() => {
        this.logging = false;
        this.showError(res.error.message || res.message);
      }, 1000);
    });
  }

  showError(e: string) {
    this.error = true;
    this.errorMsg = e;
  }


  change(e: any) {
    const key = e.keyCode;
    const value = e.target.value;
    const name = e.target.name;
    switch (name) {
      case 'username': {
        this.store.dispatch({
          type: user.SET_USERNAME, payload: value
        });
        break;
      }
      case 'password': {
        this.store.dispatch({
          type: user.SET_PASSWORD, payload: value
        });
        break;
      }
      default: {
        break;
      }
    }
    if (this._userName.trim().length < 3 || this._password.trim().length < 3) {
      this.canLogIn = false;
    } else {
      this.canLogIn = true;
    }
    if (key === 13 && this.canLogIn) {
      this.login(e);
    }
  }

}



