import { ActionReducer, Action } from '@ngrx/store';

export const SET_USERNAME = '[User] Set Name';
export const SET_PASSWORD = '[User] Set Password';
export const SET_PK = '[User] Set Instagram Id';
export const SET_PRIV = '[User] Set Privileges';

export interface State {
    pk: number;
    userName: string;
    password: string;
    priv: number;
}

export const initialState: State = {
    pk: 0,
    userName: '',
    password: '',
    priv: 0
};

export function reducer(state = initialState, action: Action): State {
    switch (action.type) {

        case SET_USERNAME: {
            return Object.assign({}, state, { userName: action. payload });
        }

        case SET_PASSWORD: {
            return Object.assign({}, state, { password: action. payload });
        }

        case SET_PK: {
            return Object.assign({}, state, { pk: action. payload });
        }

        case SET_PRIV: {
            return Object.assign({}, state, { priv: action. payload });
        }

        default: {
            return state;
        }
    }
}



